const gallery = document.querySelector('.gallery');

//**************************************************** */
function imageTemplate(imageObj) {
  return `<li class="gallery-item">
  <a class="gallery-link" href="${imageObj.original}">
    <img
      class="gallery-image"
      src="${imageObj.preview}"
      alt="${imageObj.description}"/>
  </a>
</li>`;
}

function imagesTemplate(arr) {
  return arr.map(imageTemplate).join(`\n`);
}

const markup = imagesTemplate(images);

gallery.innerHTML = markup;

//**************************************************** */

let simpleGallery = new SimpleLightbox('.gallery-link', {
  captionsData: 'alt',
  captionDelay: 250,
});

simpleGallery.on('error.simplelightbox', function (e) {
  console.log(e);
});
